import React, { memo } from 'react';
import { compose, withState, withHandlers } from 'recompose';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import DayJsUtils from '@date-io/dayjs';
import pt from 'dayjs/locale/pt';
import dayjs from 'dayjs';
import { MuiPickersUtilsProvider, DatePicker, TimePicker } from '@material-ui/pickers';
import FormHelperText from '@material-ui/core/FormHelperText';
import _get from 'lodash/get';
import _mapValues from 'lodash/mapValues';
import PropTypes from 'prop-types';
import Loading from '../../../components/Loading';
import VisitorsService from '../../../services/visitors';
import withRequest from '../../../utils/with-request';
import Session from '../../../utils/session';
import styles from './styles';

const userData = Session.getUserData();
const initialMemberData = {
  name: '',
  email: '',
  visitorCompany: '',
  visitTime: dayjs(new Date()).add(1, 'hour').minute(0),
  visitDate: new Date(),
  hostMemberId: _get(userData, 'member.id', '')
};

const VisitorModal = memo(({
  classes, open, setOpen, loading, setMemberState, resetMemberState, members, onCreateVisitor,
  memberData: { name, email, visitorCompany, visitTime, visitDate, hostMemberId },
  submitFetch, submitLoading, submitError
}) => {
  const handleOpen = () => setOpen(true);
  const handleClose = () => {
    resetMemberState();
    setOpen(false);
  };
  const errors = _mapValues(_get(submitError, 'errors', {}), (err) => err[0]);
  return (
    <div>
      <Button
        onClick={handleOpen}
        size="large"
        variant="contained"
        color="primary"
      >
        Autorizar visitante
      </Button>
      <Dialog open={open} maxWidth="md" onClose={handleClose} keepMounted>
        <DialogTitle>
          Autorizar visitante
        </DialogTitle>
        <DialogContent className={classes.dialogContent}>
          {
            loading ?
              <Loading />
              :
              <div>
                <FormControl
                  className={classes.inputFieldFullWidth}
                  fullWidth
                  error={!!errors.host_member}
                >
                  <InputLabel className={classes.inputLabel} id="hostMemberId">Pessoa visitada</InputLabel>
                  <Select
                    labelId="hostMemberId"
                    placeholder={_get(userData, 'member.name', '')}
                    fullWidth
                    className={classes.button}
                    value={hostMemberId}
                    onChange={(e) => setMemberState({ hostMemberId: e.target.value })}
                  >
                    {
                      members.map((member) => (
                        <MenuItem key={Math.random()} value={member.id}>
                          {`${member.name} ${member.lastname}`}
                        </MenuItem>
                      ))
                    }
                  </Select>
                  {errors.members ? <FormHelperText>{errors.members}</FormHelperText> : null}
                </FormControl>
                <FormControl
                  className={classes.inputFieldFullWidth}
                  fullWidth
                  error={!!errors.name}
                >
                  <TextField
                    error={!!errors.name}
                    fullWidth
                    label="Nome do visitante"
                    margin="normal"
                    InputLabelProps={{ shrink: true }}
                    value={name}
                    onChange={(e) => setMemberState({ name: e.target.value })}
                  />
                  {errors.name ? <FormHelperText>{errors.name}</FormHelperText> : null}
                </FormControl>
                <FormControl className={classes.inputField} error={!!errors.email}>
                  <TextField
                    error={!!errors.email}
                    fullWidth
                    label="E-mail do visitante"
                    margin="normal"
                    InputLabelProps={{ shrink: true }}
                    value={email}
                    onChange={(e) => setMemberState({ email: e.target.value })}
                  />
                  {errors.email ? <FormHelperText>{errors.email}</FormHelperText> : null}
                </FormControl>

                <FormControl className={classes.inputField} error={!!errors.visitorCompany}>
                  <TextField
                    error={!!errors.visitorCompany}
                    fullWidth
                    label="Empresa do visitante"
                    margin="normal"
                    InputLabelProps={{ shrink: true }}
                    value={visitorCompany}
                    onChange={(e) => setMemberState({ visitorCompany: e.target.value })}
                  />
                  {
                    errors.visitorCompany ?
                      <FormHelperText>{errors.visitorCompany}</FormHelperText>
                      :
                      null
                  }
                </FormControl>
                <FormControl className={classes.inputField} error={!!errors.visitorCompany}>
                  <MuiPickersUtilsProvider utils={DayJsUtils} locale={pt}>
                    <DatePicker
                      className={classes.calendarInputField}
                      style={styles.components}
                      label="Data da visita"
                      invalidDateMessage="Formato invlido"
                      variant="dialog"
                      value={visitDate}
                      onChange={(e) => setMemberState({ visitDate: e })}
                      minDate={new Date()}
                      format="DD/MM/YYYY"
                    />
                  </MuiPickersUtilsProvider>
                </FormControl>
                <FormControl className={classes.inputField} error={!!errors.visitorCompany}>
                  <MuiPickersUtilsProvider utils={DayJsUtils} locale={pt}>
                    <TimePicker
                      className={classes.calendarInputField}
                      style={styles.components}
                      ampm={false}
                      minutesStep={5}
                      variant="dialog"
                      label="Horrio"
                      value={visitTime}
                      onChange={(e) => setMemberState({ visitTime: e })}
                    />
                  </MuiPickersUtilsProvider>
                </FormControl>
              </div>
          }
        </DialogContent>
        <DialogActions className={classes.actions}>
          <Button onClick={handleClose} size="large">
            Cancelar
          </Button>
          <Button
            onClick={async () => {
              try {
                return (
                  await submitFetch(true),
                  onCreateVisitor(true)
                );
              } catch {
                onCreateVisitor(false);
              }
              return '';
            }}
            size="large"
            color="primary"
            variant="contained"
            disabled={submitLoading}
            className={classes.submitButton}
          >
            {submitLoading ? 'Enviando...' : 'Enviar'}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
});

VisitorModal.propTypes = {
  classes: PropTypes.object,
  open: PropTypes.bool,
  setOpen: PropTypes.func,
  members: PropTypes.array,
  setMemberState: PropTypes.func,
  resetMemberState: PropTypes.func,
  memberData: PropTypes.object,
  submitFetch: PropTypes.func,
  submitLoading: PropTypes.bool,
  submitError: PropTypes.object,
  loading: PropTypes.bool,
  onCreateVisitor: PropTypes.func
};

const withCreateVisitor = withRequest(VisitorsService.createVisitors, {
  name: 'submit',
  init: false,
  options: ({ memberData, setOpen, resetMemberState, companyId }) => ({
    variables: { ...memberData, companyId },
    onRequestFinished: () => {
      resetMemberState();
      setOpen(false);
    }
  })
});

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('open', 'setOpen', false),
  withState('memberData', 'setMemberData', initialMemberData),
  withHandlers({
    setMemberState: ({ setMemberData, memberData }) => (patch) => {
      setMemberData({ ...memberData, ...patch });
    },
    resetMemberState: ({ setMemberData }) => () => setMemberData(initialMemberData)
  }),
  withCreateVisitor
)(VisitorModal);
