import React, { memo } from 'react';
import PropTypes from 'prop-types';
import dayjs from 'dayjs';
import _get from 'lodash/get';
import CloseIcon from '@material-ui/icons/Close';
import { TableCell, TableRow } from '@material-ui/core';
import styles from './styles';

const STATUS = {
  ACCEPTED: 'Autorizado',
  PENDING: 'Pendente',
  CANCELED: 'Cancelado'
};

const VisitorsItemRow = memo(({ index, visitor, CancelVisitor }) => {
  const visitorId = _get(visitor, 'id');
  const visitorFullName = `${_get(visitor, 'name')} ${_get(visitor, 'visitor_company') || ''}`;
  const hostMemberFullName = `${_get(visitor, 'host_member.name')} ${_get(visitor, 'host_member.lastname')}`;
  const hostMemberEmail = _get(visitor, 'host_member.email');
  const hostMemberCompanyName = _get(visitor, 'company.name');
  const visitDate = _get(visitor, 'visit_date');
  const visitHour = _get(visitor, 'visit_hour');
  const checkinAt = _get(visitor, 'checkin_at');
  console.log(`${dayjs(visitDate).format('DD/MM/YY')}`);
  console.log(`${dayjs().format('DD/MM/YY')}`);
  return (
    <TableRow style={index % 2 ? styles.bgWhite : styles.bgGray}>
      <TableCell style={styles.tableCell}>
        {visitDate ? `${dayjs(visitDate).format('DD/MM/YY')}` : '--'}
        <br />
        {visitHour ? `${dayjs(visitHour).format('HH:mm')}` : '--'}
      </TableCell>
      <TableCell style={styles.tableCell}>
        {checkinAt ? dayjs(checkinAt).format('DD/MM/YY HH:mm') : '--'}
      </TableCell>
      <TableCell style={styles.tableCell}>
        {STATUS[visitor.status]}
      </TableCell>
      <TableCell style={styles.tableCell}>
        {visitorFullName}
      </TableCell>
      <TableCell style={styles.tableCell}>
        {hostMemberFullName}
        <br />
        {hostMemberCompanyName}
        <br />
        <small>{hostMemberEmail}</small>
      </TableCell>
      <TableCell style={styles.tableCell}>
        {
          (visitor.status !== 'ACCEPTED') &&
          (dayjs(visitHour).format('HH:mm') > dayjs().format('HH:mm')) &&
          <button
            type="button"
            style={styles.clearButton}
            onClick={() => CancelVisitor(visitorId)}
          >
            <CloseIcon />
          </button>
        }
      </TableCell>
    </TableRow>
  );
});

VisitorsItemRow.propTypes = {
  index: PropTypes.number,
  visitor: PropTypes.object,
  CancelVisitor: PropTypes.func
};

export default VisitorsItemRow;
